@import url(https://fonts.googleapis.com/css2?family=Merienda&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}
@font-face {
	font-family: 'The Historia Demo';
	src: url(/portfolio-page/static/media/thehistoriademo-webfont.e2db6a1d.woff2) format('woff2'),
		url(/portfolio-page/static/media/thehistoriademo-webfont.cea51a18.woff) format('woff');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'The Historia Demo';
	src: url(/portfolio-page/static/media/thehistoriademo-webfont.e2db6a1d.woff2) format('woff2'),
		url(/portfolio-page/static/media/thehistoriademo-webfont.cea51a18.woff) format('woff');
	font-weight: normal;
	font-style: normal;
}
/*  
#F7B538 -mustardy
#1F2235 -indigoey
#FE4A57 -reddish
*/
* {
	/* border: 1px solid red; */
	/* outline: 1px solid red; */
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html,
body {
	scroll-behavior: smooth;
	width: 100vw;
	overflow-x: hidden;
}

.App {
	text-align: center;
	min-height: calc (100vh - 120px);
	background: #242a50;
	border: 1px solid transparent;
}

#navbar {
	background: #1f2235;
	height: 70px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-size: 22px;
	max-width: 100vw;
	position: relative;
}

.nav-menu {
	display: flex;
	list-style: none;
	text-align: center;
	/* justify-content: start; */
}

#navbar h2 {
	padding-left: 30px;
}

.nav-item {
	display: flex;
	align-items: center;
	height: 70px;
	line-height: 20px;
	padding: 15px;
}

.nav-dev {
	display: flex;
	align-items: center;
	height: 70px;
	line-height: 20px;
	color: #fe4a57;
	text-decoration: none;
	padding: 8px 15px;
	font-family: 'The Historia Demo', sans-serif;
}

.nav-links {
	color: #fe4a57;
	text-decoration: none;
	padding: 8px 14px;
	font-family: 'The Historia Demo', sans-serif;
	font-size: 34px;
}

.social-icons {
	color: #fe4a57;
	font-size: 32px;
	padding-right: 30px;
	display: flex;
}

.social-icons a {
	padding-left: 10px;
}

.social-icons a:visited {
	color: #fe4a57;
}

.nav-links:hover {
	background-color: #f7b538;
	color: #1f2235;
	border-radius: 4px;
	transition: all 0.2s ease-out;
	cursor: pointer;
}

.fa-bars {
	color: #fe4a57;
}
.fa-times:before {
	color: #fe4a57;
}

.menu-icon {
	display: none;
}

@media screen and (max-width: 768px) {
	.nav-menu {
		display: flex;
		flex-direction: column;
		width: 100vw;
		max-width: 100vw;
		height: calc(100vh - 120px);
		position: absolute;
		top: 70px;
		left: -100vw;
		opacity: 1;
		transition: all 0.5s ease;
	}

	.nav-menu.active {
		background: #1f2235;
		left: 0;
		opacity: 1;
		transition: all 0.5s ease;
		z-index: 1;
	}

	.nav-links {
		text-align: center;
		padding: 16px;
		width: 100vw;
		display: table;
		font-size: 30px;
		margin-left: -30px;
	}

	.nav-links:hover {
		background-color: #f7b538;
		color: #1f2235;
		border-radius: 0;
	}

	#navbar-logo {
		position: absolute;
		top: 0;
		left: 0;
		transform: translate(25%, 50%);
	}

	.menu-icon {
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		transform: translate(-100%, 60%);
		font-size: 29px;
		cursor: pointer;
	}

	.fa-times {
		font-size: 32px;
	}

	.social-icons {
		width: 90px;
		position: absolute;
		left: calc(50% - 45px);
		font-size: 28px;
	}
}

@media screen and (min-width: 1700px) {
}

#about {
	/* min-height: calc(100vh - 120px); */
	background: #242a50;
	max-width: 1600px;
	margin: 0 auto;
	padding-bottom: 25px;
}
/* /////////////////GREETING///////////////////// */
#hello {
	font-size: 24px;
	color: #f7b538;
	font-family: 'Merienda', cursive;
	padding-top: 30px;
}
#hello h1 {
	font-family: 'The Historia Demo';
	font-size: 160px;
	background: linear-gradient(to top, #fa320f, #ffc738);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	font-weight: 500;
}

/* /////////////////INTRO//////////////////////// */

.intro {
	display: grid;
	grid-template-columns: 2fr 1fr 2fr;
	position: relative;
	padding-top: 80px;
}
.intro p {
	font-family: 'Merienda', cursive;
	font-size: 24px;
	color: #f7b538;
	width: 80%;
	margin: 15px auto;
}
.intro hr {
	border: 1px solid #fe4a57;
}

.intro div {
	position: relative;
	text-align: justify;
}
.profile-pic {
	overflow: hidden;
	border-radius: 50%;
	width: 205px;
	height: 205px;
	/* /* position: absolute; */
	left: calc(50% - 100px);
	top: calc(50% - 150px);
}

.profile-pic img {
	object-fit: cover;
	transform: scale(0.52) translateX(-260px) translateY(-280px);
}
/* .intro h2 {
	position: absolute;
	top: 25%;
	left: 70%;
} */
#about > h2 {
	font-family: 'Merienda', cursive;
	color: #f7b538;
	font-size: 32px;
	font-weight: 300;
}
/* ///////////////////////////////////////////// */

@media screen and (max-width: 768px) {
	.intro {
		display: flex;
		flex-direction: column;
	}
	.intro :nth-child(1) {
		order: 3;
	}
	.intro :nth-child(3) {
		order: 3;
	}
	#hello h1 {
		font-size: 120px;
	}
	#hello h2 {
		font-size: 30px;
	}
	#about > h2 {
		font-size: ;
	}
}
@media screen and (max-width: 580px) {
	#hello h1 {
		font-size: 100px;
	}
	#hello h2 {
		font-size: 24px;
	}
	#about > h2 {
		font-size: 24px;
	}
}

#skills {
	padding: 50px;
	position: relative;
	background: #242a50;
	color: #fe4a57;
	font-family: 'The Historia Demo', sans-serif;
	text-align: center;
}

#skills div {
	line-height: 2;
}

#skills h2 {
	font-size: 42px;
	font-weight: 300;
}

#skills h3 {
	font-size: 26px;
	font-weight: 300;
	font-family: 'Merienda', cursive;
	color: #f7b538;
}

.techstack {
	display: flex;
	flex-flow: row;
	width: 60%;
	flex-wrap: wrap;
	justify-content: space-around;
	padding-top: 40px;
	padding: 20px;
	text-align: center;
	margin: 0 auto;
}

.tech-icons {
	margin: 10px 20px;
	background-color: #242a50;
	border-radius: 5px;
}

.iconify {
	font-size: 60px;
	height: 100%;
	padding: 0;
}

@media screen and (max-width: 1024px) {
	.techstack {
		width: 70%;
	}
	.iconify {
		font-size: 50px;
	}
	.tech-icons {
		margin: 10px 10px;
		background-color: #242a50;
		border-radius: 5px;
	}
}
@media screen and (max-width: 720px) {
	.techstack {
		width: 90%;
	}
}

#projects {
	margin-bottom: 120px;
	padding-top: 0px;
	background-color: #242a50;
	min-height: 50vh;
	display: grid;
}

#projects h2 {
	color: #fe4a57;
	font-family: 'The Historia Demo', sans-serif;
	font-weight: 300;
	font-size: 62px;
}

.projects-div {
	margin: 20px auto;
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	max-width: 1400px;
}

.project-card {
	height: 200px;
	width: 380px;
	border-radius: 5px;
	margin-bottom: 20px;
	position: relative;
}

.project-card:hover {
	cursor: pointer;
}

.btn {
	height: 30px;
	width: 80px;
	border: 2px solid #fe4a57;
	border-radius: 10px;
	opacity: 1;
	font-size: 16px;
	font-family: 'Merienda', cursive;
}

.card {
	position: relative;
	margin-top: 50px;
	width: 500px;
	height: 300px;
	border-radius: 20px;
	font-family: 'The Historia Demo', sans-serif;
	overflow: hidden;
}

.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0);
	transition: background 0.5s ease;
}

.card:hover .overlay {
	display: block;
	background: rgba(0, 0, 0, 0.8);
}

.card img {
	position: absolute;
	width: 500px;
	height: 300px;
	left: 0;
}

.title {
	position: absolute;
	width: 500px;
	top: 220px;
	font-weight: 300;
	font-size: 35px;
	text-align: center;
	color: #fe4a57;
	transition: top 0.5s ease;
	z-index: 1;
	opacity: 0;
}
.proj-description {
	font-family: 'Merienda', cursive;
	font-size: 16px;
	text-align: center;
	padding: 0 30px;
	line-height: 2;
	color: #f7b538;
}

.card:hover .title {
	top: 30px;
	opacity: 1;
}

.button-wrap {
	position: absolute;
	width: 500px;
	left: 0;
	bottom: 20px;
	text-align: center;
	opacity: 0;
	transition: opacity 0.35s ease;
}
.card:hover .button-wrap {
	opacity: 1;
}
.btn {
	background-color: #1f2235;
	color: #fe4a57;
}
.btn:hover {
	cursor: pointer;
	background-color: #f7b538;
	color: #1f2235;
	border: 2px solid #1f2235;
}

.button-wrap a {
	width: 100px;
	padding: 5px;
	text-align: center;
	z-index: 1;
}
@media screen and (max-width: 1024px) {
	#projects h2 {
		font-size: 56px;
		width: 75%;
		margin: 0 auto;
	}
}

@media screen and (max-width: 768px) {
	#projects h2 {
		font-size: 48px;
		width: 65%;
	}
	.card {
		position: relative;
		margin-top: 50px;
		width: 420px;
		height: 280px;
		border-radius: 20px;
		font-family: 'The Historia Demo', sans-serif;
		overflow: hidden;
	}
	.card img {
		width: 420px;
		height: 280px;
	}
	.overlay {
		display: block;
		background: rgba(0, 0, 0, 0.65);
	}
	.title {
		width: 420px;
		top: 20px;
		left: 0;
		font-weight: 300;
		font-size: 35px;
		color: #fe4a57;
		transition: top 0.5s ease;
		z-index: 1;
		opacity: 1;
		margin: 0 auto;
	}
	.proj-description {
		font-size: 14px;
		padding: 0;
		line-height: 2;
		width: 80%;
		margin: 0 auto;
		text-align: center;
		color: white;
	}
	.button-wrap {
		opacity: 1;
		width: 420px;
	}
	.card:hover .title {
		top: 20px;
	}
	.btn {
		background-color: #f7b538;
		color: #1f2235;
	}
}

@media screen and (max-width: 540px) {
	#projects h2 {
		font-size: 40px;
		width: 65%;
	}
	.card {
		position: relative;
		margin-top: 50px;
		width: 360px;
		height: 240px;
		border-radius: 20px;
		font-family: 'The Historia Demo', sans-serif;
		overflow: hidden;
	}
	.card img {
		width: 360px;
		height: 240px;
	}
	.title {
		width: 360px;
		top: 20px;
		left: 0;
		font-weight: 300;
		font-size: 26px;
		color: #fe4a57;
		transition: top 0.5s ease;
		z-index: 1;
		opacity: 1;
		margin: 0 auto;
	}
	.button-wrap {
		opacity: 1;
		width: 360px;
	}
	.proj-description {
		font-size: 12px;
	}
}

#contact {
	display: grid;
	display: flex;
	flex-direction: column;
	width: 40vw;
	max-width: 800px;
	min-width: 360px;
	margin: 0 auto;
	margin-bottom: 100px;
}

#contact {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

#contact > h2 {
	margin-bottom: 30px;
	color: #fe4a57;
	font-family: 'The Historia Demo', sans-serif;
	font-weight: 300;
	font-size: 40px;
}

#contact > input,
textarea {
	padding: 10px;
	border-radius: 3px;
	margin-bottom: 20px;
	/* border: none; */
	background: #fff;
	font-size: 16px;
	color: rgb(0, 0, 32);
	outline: none;
	border: 3px solid #fe4a57;
}

#contact > input:focus,
textarea:focus {
	border: 3px solid #f7b538;
}

#contact > textarea {
	height: 150px;
	max-width: 100%;
	min-height: 100px;
}

#contact > label {
	padding-bottom: 5px;
	color: #f7b538;
	font-weight: bold;
	font-family: 'Merienda', cursive;
	font-size: 18px;
}

#contact > button {
	color: #fe4a57;
	background: #1f2235;
	text-decoration: none;
	padding: 8px 14px;
	font-family: 'The Historia Demo', sans-serif;
	font-size: 26px;
	width: 150px;
	border-radius: 10px;
	margin: 0 auto;
}

#contact > button:hover {
	background-color: #f7b538;
	color: #1f2235;
	transition: all 0.3s ease-out;
	cursor: pointer;
}
::-webkit-input-placeholder {
	color: #fe4a57;
	font-family: 'Merienda', cursive;
	font-size: 12px;
}
.contact-info {
	margin: 20px auto;
	width: 100%;
	font-size: 28px;
	color: #f7b538;
}
.contact-info > * {
	padding: 10px;
}
.contact-info a {
	color: #f7b538;
}
.contact-info a:visited {
	color: #f7b538;
}

.home-top {
	height: 40px;
	color: #f7b538;
	text-decoration: none;
	padding: 8px 15px;
	font-family: 'Merienda', cursive;
	width: 25%;
	font-size: 18px;
	min-width: 180px;
	margin: 0 auto;
	border: 1px #f7b538 solid;
	border-radius: 8px;
}

#footer {
	position: fixed;
	bottom: 0;
	max-width: 100vw;
	width: 100%;
	background-color: #1f2235;
	height: 50px;
	align-content: center;
	font-family: 'Merienda', cursive;
	z-index: 1;
	overflow: hidden;
}

#footer p {
	font-size: 17px;
	text-align: center;
	color: #fe4a57;
}

#footer hr {
	width: 75%;
	border: 1px solid #fe4a57;
	margin: 0 auto;
}

